import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { IndexComponent } from './components/index.component';
import { LoginComponent } from './components/login.component';
import { RegisterSupplierComponent } from './components/register-supplier.component';
import { RegisterMakerComponent } from './components/register-maker.component';

const routes: Routes = [
	{ path: '', component: IndexComponent },
	{ path: 'login',  component: LoginComponent },
	{ path: 'register-supplier',  component: RegisterSupplierComponent },
	{ path: 'register-maker',  component: RegisterMakerComponent },
];

@NgModule({
	imports: [ RouterModule.forRoot(routes) ],
	exports: [ RouterModule ]
})
export class AppRoutingModule {}


